import React, {Component} from 'react';
import './resumen.scss';
import ResumenItem from './resumenItem/resumenItem';
import SesionMap from './sesionMap.js';

import sesion01 from '../../images/imagenes/sesion01.jpg';
import sesion02 from '../../images/imagenes/sesion02.jpg';
import sesion03 from '../../images/imagenes/sesion03.jpg';
import sesion04 from '../../images/imagenes/sesion04.jpg';
import sesion05 from '../../images/imagenes/sesion05.jpg';

class Resumen extends Component {

	sesionMap = {};
		
	constructor (){
		super();
		this.sesionMap = new SesionMap();
		
	}
	
	getSesionContent (sesionId){
	
		let sesion = this.sesionMap.getMap()[sesionId];

		return (<td id={"sesionItem" + sesionId} data-aos={sesion.aosEffect} data-aos-duration="1000" 
			rowSpan= {sesion.rowspan} style={{width: sesion.ancho + 'vw', height: sesion.alto + 'vh'}}>
				<ResumenItem nombre={sesion.nombre}
					imagen={sesion.portada} itemId="resumenItem01"/>							
			</td>);
	}
	
	render (){
		return (
			<section id="resumen">
				<h1>Nuestros proyectos</h1>
				<div id="resumenItems">
					<table>
						<tbody>
							<tr>
								{this.getSesionContent(0)}
								{this.getSesionContent(1)}						
							</tr>
							<tr>
								{this.getSesionContent(2)}						
							</tr>
						</tbody>
					</table>
					
				</div>
			</section>
		);
	}
	
}

export default Resumen;